            getNewData(data){
               var newData={
                   xNameData:[],
                   helpYData: [],
                   abnormalData:[],
                   concernData:[],
                   normalData:[]
               };
               for(var i=0;i<data.length;i++){
                   newData.xNameData.push(data[i].name);
                   if(data[i].level){  //等级不为空
                       if('A'==data[i].level){
                           newData.helpYData.push(1550);
                           newData.abnormalData.push(0);
                           newData.concernData.push(0);
                           newData.normalData.push(400);
                       }else if('B'==data[i].level){
                           newData.helpYData.push(1050);
                           newData.abnormalData.push(0);
                           newData.concernData.push(400);
                           newData.normalData.push(0);
                       }else if('C'==data[i].level){
                           newData.helpYData.push(550);
                           newData.abnormalData.push(400);
                           newData.concernData.push(0);
                           newData.normalData.push(0);
                       }else if('D'==data[i].level){
                           newData.helpYData.push(0);
                           newData.abnormalData.push(400);
                           newData.concernData.push(0);
                           newData.normalData.push(0);
                       }
                   }else{  //等级为空
                       newData.helpYData.push(0);
                       newData.abnormalData.push(0);
                       newData.concernData.push(0);
                       newData.normalData.push(0);
                   }
               }
               return newData
            },
            showCompany:function(){
                this.tempCompany = this.currentCompany.codeName;
                this.popupVisible = true;

            },
            showYear:function(){
