        transform: rotate(45deg);
        position: relative;
        top: -0.07rem;
        margin-left: 0.14rem;
    }
    .select-company{
        text-align: center;
        margin-top: 1rem;
        height:100%;
        overflow-y:scroll;
        -webkit-overflow-scrolling: touch;
    }
    .select-company>button{
        display: inline-block;
        background: transparent;
        float: left;
        height: 0.6rem;
        line-height: 0.6rem;
        width: 1.4rem;
        border-radius: 1rem;
        margin:0.05rem 0;
        border: 1px solid transparent;
        box-sizing: border-box;
    }
    .select-company>button.selected{
        color: #116dfc;
        border: 1px solid #116dfc
    }
    .select-company>button:nth-child(even){
        margin-right: 1.26rem;
        margin-left: 0.62rem;
    }
    .select-company>button:first-child{
        margin-right: 5.48rem;
        margin-left: 0.62rem;
        margin-top: 0.26rem;
    }
    .select-company>button:nth-child(2n+3){
        margin-right: 1.56rem;
        margin-left: 1.26rem;
    }
    .select-title{
        position: absolute;
        top: 0;
        left: 0;
        height: 1rem;
        right: 0;
        margin: 0 0.3rem;
        border-bottom: 1px solid #ECECEC;
        line-height: 1rem;
        text-align: center;
        font-size: 0.32rem;
        color: #3B4053;
        letter-spacing: 0;
        box-sizing: border-box;
        /* background: #fff;
         z-index: 1;*/
    }
    .select-title>.cancel{
        opacity: 0.7;
        font-size: 0.28rem;
        color: #3B4053;
        float: left;
    }
    .select-title>.confirm{
        opacity: 0.7;
        font-size: 0.28rem;
        color: #116DCF;
        float: right;
    }
    .blank-fill{
        display: inline-block;
        width: 100%;
        height: 1rem;
    }
    .backImg{
        background: url("../../static/image/pic_zhkb_circle.png");
        width: 5.3rem;
        height:3.586rem;
        margin-left: 1.1rem;
        display: inline-block;
        background-repeat: no-repeat;
        background-size: 100% 100%;
        background-position: 100%;
        margin-top: .4rem;
    }
    /*滑块未选中样式*/
    .item_btn>>> .mint-button-text{
        opacity: 0.3;
        font-family: PingFangSC-Regular;
        font-size: .26rem;
        color: #116DCF;
        letter-spacing: 0;
        line-height: .35rem;
    }
    /*滑块样式*/
    .item {
        margin: 0;
        overflow-x: auto;
    }
    .item::-webkit-scrollbar{
        width: 0;
        height: 0;
        background: none;
    }
    .item_scroll{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 12.3rem;
        position: relative;
        height: 1.16rem;
        padding: 0 .3rem;
    }
    .item_btn{
        margin: 0 .3rem 0 0;
        padding: 0;
        width: 1.8rem;
        background-color: #ffffff;
        color: #303030;
        height: .74rem;
        box-shadow: 0 0 0 #ffffff;
        border-radius: 0;
    }
    .item_btn:last-of-type{
        margin: 0;
    }
    .item_scroll>>> .mint-button{
        overflow: visible;
    }
    .item_scroll>>> .mint-button::after{
        background-color: rgba(0,0,0,0);
    }
    .active_bac{
        position: relative;
        height: 100%;
        /*display: flex;*/
        /*justify-content: center;*/
    }
    .active_bac:before{
        content: "";
        position: absolute;
        bottom:0;
        width: 1.3rem;
        height:.04rem;
        background-color: #116DCF;
        border-radius: .4rem;
        left: .1rem;
    }
    .active_bac>>> .mint-button-text{
        font-family: PingFangSC-Regular;
        font-size: .26rem;
        color: #116DCF;
        letter-spacing: 0;
        opacity: 1;
    }
    .time_sty{
        height: 1.4rem;
        line-height: 1.4rem;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #ffffff;
    }
    .time_sty>>> .mint-tab-item{
        flex: none;
        padding: 0;
    }
    .time_sty .timesty_rad:nth-of-type(1){
        margin-right: .7rem;
    }
    .time_sty .timesty_rad{
        width: 2.56rem;
        height: .56rem;
        border: .02rem solid #116DCF;
        border-radius: 1rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .timesty_rad>>> .mint-tab-item-label{
        font-family: PingFangSC-Regular;
        font-size: .28rem;
        color: #116DCF;
        letter-spacing: .04rem;
    }
    .mint-tabbar>>> .mint-tab-item.is-selected{
        background-color: #ffffff;
        color: #000;
        border:0 solid #ffffff;
        padding: .02rem;
    }
    .time_bac{
        background-image: linear-gradient(-180deg, #50A2FF 0%, #116DCF 100%);
        border-radius: 1rem;
    }
    .time_bac>>> .mint-tab-item-label{
        color: #ffffff;
    }
</style>



